import React from 'react';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import imgs1 from '../Assets/Images/house-1836070_1280.jpg';
import imgs2 from '../Assets/Images/kitchen-2400367_1280.jpg';
import imgs3 from '../Assets/Images/home-3593832_1280.jpg';
import imgs4 from '../Assets/Images/house-2483336_1280.jpg';
import '../Styles/cardslider.css'; 

const Cardslider = () => {
  const propertyData = [
    {
      id: 1,
      title: 'Beautiful Family House',
      description: 'A beautiful and spacious family house located in the suburbs. Perfect for raising a family.',
      imageUrl: imgs1,
    },
    {
      id: 2,
      title: 'Modern Apartment',
      description: 'A sleek and modern apartment in the heart of the city, close to all amenities.',
      imageUrl: imgs2,
    },
    {
      id: 3,
      title: 'Cozy Cottage',
      description: 'A charming and cozy cottage in a quiet rural area, ideal for a peaceful retreat.',
      imageUrl: imgs3,
    },
    {
      id: 4,
      title: 'Luxurious Villa',
      description: 'A luxurious villa with stunning sea views and top-of-the-line amenities.',
      imageUrl: imgs4,
    }
  ];

  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 7,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  return (
    <>
      <div className="container mt-5">
        <h1 className="text-primary">Featured Properties</h1>
      </div>
      <Carousel 
        responsive={responsive} 
        className="mt-5 container carousel-container"
        autoPlay={true}
        autoPlaySpeed={2000} 
        infinite={true} 
        removeArrowOnDeviceType={['tablet', 'mobile']} 
      >
        {propertyData.map((property) => (
          <Card key={property.id} style={{ width: '18rem' }}>
            <Card.Img variant="top" src={property.imageUrl} className="card-img-top" />
            <Card.Body className="non">
              <Card.Title className="dis">{property.title}</Card.Title>
              <Card.Text className="showoff">{property.description}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </Carousel>
     
      <div className="cline bg3 mt-5 mb-4"></div>
    </>
  );
};

export default Cardslider;
