import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import axios from 'axios';
import emailjs from 'emailjs-com';

const Contact = () => {
  const { id } = useParams();
  const [formData, setFormData] = useState({ id: id, name: '', email: '', message: '' });
  const [error, setError] = useState('');
  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // EmailJS configuration
    const serviceId = 'service_yzn8r05';
    const templateId = 'template_l5n476t';
    const userId = 'SARvzK93VL6K7-7aO';

    const templateParams = {
      to_name: 'Abdul Rehman', // Customize as needed
      from_name: formData.name,
      message: formData.message,
      reply_to: formData.email,
    };

    try {
      // Send email using EmailJS
      await emailjs.send(serviceId, templateId, templateParams, userId);

      // Make API call using Axios
      const response = await axios.post('https://real-estate.free.beeceptor.com/api/contact', formData);

      if (response.status === 200) {
        setFormData({ id: id, name: '', email: '', message: '' });
        setError('');
        setSubmitted(true);
      } else {
        setError('Failed to submit form. Please try again later.');
      }
    } catch (error) {
      if (error.response && error.response.status === 429) {
        setError('Too many requests. Please try again later.');
      } else {
        setError('Error submitting form. Please try again later.');
      }
      console.error('Error submitting form:', error);
    }
  };

  return (
    <div className='container'>
      <h2>Contact Us</h2>
      {submitted && (
        <Alert variant="success" onClose={() => setSubmitted(false)} dismissible>
          Your message has been submitted successfully!
        </Alert>
      )}
      {error && <Alert variant="danger" onClose={() => setError('')} dismissible>{error}</Alert>}
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="name">
          <Form.Label>Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter your name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </Form.Group>
        <Form.Group controlId="email">
          <Form.Label>Email address</Form.Label>
          <Form.Control
            type="email"
            placeholder="Enter your email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </Form.Group>
        <Form.Group controlId="message">
          <Form.Label>Message</Form.Label>
          <Form.Control
            as="textarea"
            rows={3}
            placeholder="Enter your message"
            name="message"
            value={formData.message}
            onChange={handleChange}
            required
          />
        </Form.Group>
        <Button variant="primary" type="submit" className='my-4'>
          Submit
        </Button>
      </Form>
    </div>
  );
};

export default Contact;