import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import axios from 'axios';
import noimg from '../Assets/Images/360_F_473254957_bxG9yf4ly7OBO5I0O5KABlN930GwaMQz.jpg';

function ShowContact() {
  const [realEstates, setRealEstates] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('https://real-estate.free.beeceptor.com/api/contact');
        setRealEstates(response.data);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  return (
    <Container>
      <h1>Real Estates</h1>
      <Row>
        {realEstates.map((realEstate) => (
          <Col key={realEstate.id} xs={12} sm={6} md={4} lg={3}>
            <Link to={`/contact/${realEstate.id}`}> {/* Wrap each card in Link component */}
              <Card className='my-4'>
                <Card.Body>
                  <Card.Title>{realEstate.name}</Card.Title>
                  <Card.Text>
                    Location: {realEstate.email}
                    <br />
                    Price: ${realEstate.message}
                  </Card.Text>
                </Card.Body>
              </Card>
            </Link>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default ShowContact;
