import React, { useState } from 'react';
import { Form, Button, Alert, Card } from 'react-bootstrap'; 
import axios from 'axios';
import ShowContact from './ShowContact';
import Cards from './Cards';

const Dashboard = () => {
  const [formData, setFormData] = useState({
    imageUrl: '',
    houseCategory: '',
    location: '',
    price: '',
    area: ''
  });

  const [submitted, setSubmitted] = useState(false); 

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      
      const response = await axios.post('https://check.free.beeceptor.com/api/cards', formData);
      console.log(response.data); 
      
      setFormData({
        imageUrl: '',
        houseCategory: '',
        location: '',
        price: '',
        area: ''
      });
      
      setSubmitted(true);
    } catch (error) {
      console.error('Error submitting data:', error);
    }
  };

  return (
    <div className="container">
      <h2 className="mb-4">Post New Data</h2>
      {submitted && ( 
        <Alert variant="success">Form submitted successfully!</Alert>
      )}
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="imageUrl">
          <Form.Label>Image URL</Form.Label>
          <Form.Control
            type="text"
            name="imageUrl"
            value={formData.imageUrl}
            onChange={handleChange}
            placeholder="Enter image URL"
            required
          />
        </Form.Group>

        <Form.Group controlId="houseCategory">
          <Form.Label>House Category</Form.Label>
          <Form.Control
            type="text"
            name="houseCategory"
            value={formData.houseCategory}
            onChange={handleChange}
            placeholder="Enter house category"
            required

          />
        </Form.Group>

        <Form.Group controlId="location">
          <Form.Label>Location</Form.Label>
          <Form.Control
            type="text"
            name="location"
            value={formData.location}
            onChange={handleChange}
            placeholder="Enter location"
            required

          />
        </Form.Group>

        <Form.Group controlId="price">
          <Form.Label>Price</Form.Label>
          <Form.Control
            type="text"
            name="price"
            value={formData.price}
            onChange={handleChange}
            placeholder="Enter price"
            required

          />
        </Form.Group>

        <Form.Group controlId="area">
          <Form.Label>Area</Form.Label>
          <Form.Control
            type="text"
            name="area"
            value={formData.area}
            onChange={handleChange}
            placeholder="Enter area"
            required

          />
        </Form.Group>

        <Button variant="primary" type="Create" className='my-5'>
          Create
        </Button>
      </Form>
      <ShowContact/>
      <Cards/>
    </div>
  );
};

export default Dashboard;
