import React, { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const OurAgentsPage = () => {
  const [agents, setAgents] = useState([]);

  useEffect(() => {
    fetch('https://randomuser.me/api/?results=6')
      .then(response => response.json())
      .then(data => {
        setAgents(data.results.slice(0,4));
      })
      .catch(error => {
        console.error('Error fetching agents:', error);
      });
  }, []);

  return (
    <Container className="py-5">
      <h1 className="text-center mb-5">Our Agents</h1>
      <Row xs={1} sm={2} md={3} lg={4} className="g-4">
        {agents.map(agent => (
          <Col key={agent.login.uuid}>
            <Card>
              <Card.Img variant="top" src={agent.picture.large} />
              <Card.Body>
                <Card.Title>{agent.name.first} {agent.name.last}</Card.Title>
                <Card.Text>
                  <strong>Email:</strong> {agent.email}
                </Card.Text>
                <Card.Text>
                  <strong>Phone:</strong>{agent.phone}
                </Card.Text>
                <Card.Text>
                  <strong>Location:</strong> {agent.location.city}, {agent.location.country}
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default OurAgentsPage;
