import React from 'react';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

function Nav_bar() {
  return (
    <div>
      <Navbar expand="lg" className='fs-6 fw-normal backclr height  sticky-top'  >
        <Container fluid>
          <Navbar.Brand href="/"><h3 className='text-dark'>AlFalah Real-Estate</h3></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll" style={{ backgroundColor: 'white', maxHeight: '300px', width: '100%', overflow: 'hidden', marginLeft: '10px' }}>
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ width: '100%' }}
              navbarScroll
            >
              <Nav.Link as={Link} to="/" className='h6 text-dark'>Home</Nav.Link>
              <Nav.Link as={Link} to="/about" className='h6 text-dark'>About Us</Nav.Link>
              <Nav.Link as={Link} to="/agent" className='h6 text-dark'>Our Agent</Nav.Link>
              <Nav.Link as={Link} to="/contact/main" className='h6 text-dark'>Contact Us</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className="container-fluid line my-100"></div>
    </div>
  );
}

export default Nav_bar;
