import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom'; 
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import axios from 'axios';
import noimg from '../Assets/Images/360_F_473254957_bxG9yf4ly7OBO5I0O5KABlN930GwaMQz.jpg';

function Cards() {
  const [realEstates, setRealEstates] = useState([]);
  const location = useLocation();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('https://real-estate.free.beeceptor.com/api/real-estate');
        setRealEstates(response.data);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const handleDelete = async (id) => {
    try {
      await axios.delete(`https://real-estate.free.beeceptor.com/api/real-estate/${id}`);
      setRealEstates(realEstates.filter(realEstate => realEstate.id !== id));
    } catch (error) {
      console.error('Error deleting data:', error);
    }
  };

  return (
    <Container>
      <h1>Real Estates</h1>
      <Row>
        {realEstates.map((realEstate) => (
          <Col key={realEstate.id} xs={12} sm={6} md={4} lg={3}>
            <Card className='my-4'>
              <div style={{ position: 'relative' }}>
                <Card.Img variant="top" src={realEstate.imageUrl ? realEstate.imageUrl : noimg} />
                {location.pathname.includes('showdata') && (
                  <Button 
                    variant="danger" 
                    style={{ 
                      position: 'absolute', 
                      top: '10px', 
                      right: '10px', 
                      borderRadius: '50%' 
                    }}
                    onClick={() => handleDelete(realEstate.id)}
                  >
                    &times;
                  </Button>
                )}
              </div>
              <Card.Body>
                <Card.Title>{realEstate.houseCategory}</Card.Title>
                <Card.Text>
                  Location: {realEstate.location}
                  <br />
                  Price: ${realEstate.price}
                  <br />
                  Area: {realEstate.area} sqft
                </Card.Text>
                <Link to={`/contact/${realEstate.id}`}>
                  <Button variant="primary">Contact</Button>
                </Link>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Cards;
